import { useState } from "react"
import { useNavigate } from "react-router-dom"
import { useUser } from "@clerk/clerk-react"
import { Watchlist } from "@rp/common/api-types"
import { formatTime } from "@rp/common/utils"
import { useAccount } from "@rp/react/common/providers"
import { UserAvatar } from "@rp/react/web/common/base-components"
import { HubTableRow } from "@rp/react/web/common/composite-components"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box } from "@rp/react/web/mui/core"
import { WATCHLIST_EDIT, WATCHLIST_ID_ROUTE } from "@rp/react/web/router"
import { useWatchlistsActions } from "@rp/react/web/watchlists"
import { getUserName } from "../../Searches/getUserName"
import {
  WatchlistTableDesktopActions,
  WatchlistTableMobilesActions,
} from "../WatchlistTableActions"

type Props = {
  item: Watchlist
  columnsWidth: Array<string>
  filterTerm: string
}

const FindResultWatchlistsItem = ({ item, columnsWidth, filterTerm }: Props) => {
  const navigate = useNavigate()
  const { user } = useUser()
  const { chat, share, copy, search } = useWatchlistsActions()
  const { isMobile } = useDeviceInfo()
  const [openMenu, setOpenMenu] = useState(false)
  const { hasEntitlement } = useAccount()

  const mobileActions = hasEntitlement("search") ? [chat, share, copy, search] : [chat, share, copy]
  const desktopActions = hasEntitlement("search") ? [share, copy, search] : [share, copy]

  return (
    <HubTableRow
      componentProps={{ filterTerm }}
      showCompanyIcon={item.shared.company.permission !== "undefined"}
      selected={openMenu}
      onRowClick={() => {
        if (isMobile) {
          navigate(WATCHLIST_EDIT.replace(":id", item.id))
        } else {
          navigate(WATCHLIST_ID_ROUTE.replace(":id", item.id))
        }
      }}
      mobileActions={
        <WatchlistTableMobilesActions
          openMenu={openMenu}
          setOpenMenu={setOpenMenu}
          actions={mobileActions}
          item={item}
        />
      }
      columnsWidth={columnsWidth}
      columns={[
        { text: item.name },
        {
          text: `${formatTime(item.lastInteracted ?? item.lastUpdated)} Â· ${getUserName(
            { userEmail: user?.primaryEmailAddress?.emailAddress, userId: user?.id },
            item,
          )}`,
          node: (
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <UserAvatar ownerName={item.ownerName} />
              <EllipsisTooltip tooltipText={item.ownerName} typographyProps={{ fontSize: 14 }}>
                {getUserName(
                  { userEmail: user?.primaryEmailAddress?.emailAddress, userId: user?.id },
                  item,
                )}
              </EllipsisTooltip>
            </Box>
          ),
        },
        {
          node: (
            <WatchlistTableDesktopActions
              openMenu={openMenu}
              setOpenMenu={setOpenMenu}
              actions={desktopActions}
              item={item}
            />
          ),
        },
      ]}
    />
  )
}
export { FindResultWatchlistsItem }
